    color: #7a574d;
    font-size: 26px;
    transition: 0.3s; }
    .nav__link:hover {
      color: #50352e; }
    .nav__link_contacts {
      font-size: 28px; }

.logo {
  position: relative;
  z-index: 2;
  width: 300px; }
  .logo__link {
    display: block;
    width: 100%;
    text-decoration: none;
    color: #fffbdb; }
  .logo__image {
    display: block;
    width: 100%;
    height: auto; }
  .logo__slogan {
    margin: 0;
    font-size: 24px;
    font-style: italic;
    padding-top: 0.4em; }

.socials__list {
  list-style: none;
  display: flex;
  padding: 0;
  margin: 0 37px 0 0; }

.socials__item:not(:last-child) {
  margin-right: 10px; }

.socials__link {
  display: flex;
  align-items: center;
  height: 20px;
  padding-left: 25px;
  font-size: 16px;
  transition: 0.3s; }
  .socials__link:hover {
    text-decoration: none;
    opacity: 0.8; }
  .socials__link_vk {
    background: url("../img/icons/Footer_vk-ico.png") no-repeat left top;
    background-size: auto 100%; }
  .socials__link_fb {
    background: url("../img/icons/Footer_fb-ico.png") no-repeat left top;
    background-size: auto 100%; }

.mobile-button {
  transition: .3s;
  width: 40px;
  height: 28px;
  z-index: 15;
  display: none;
  position: relative;
  flex-shrink: 0; }
  .mobile-button span {
    display: block;
    position: absolute;
    height: 4px;
    width: 40px;
    left: 0;
    position: absolute;
    top: 50%;
    margin-top: -2px;
    border-radius: 4px;
    background-color: #7a574d;
    transition: .3s; }
    .mobile-button span:before, .mobile-button span:after {
      display: block;
      content: "";
      height: 4px;
      border-radius: 2px;
      width: 100%;
      background-color: #7a574d;
      position: absolute;
      transition: .3s;
      left: 0; }
    .mobile-button span:before {
      top: -12px; }
    .mobile-button span:after {
      bottom: -12px; }
  .mobile-button_active span {
    background-color: transparent !important; }
    .mobile-button_active span:before {
      transform: rotate(45deg);
      top: 0px; }
    .mobile-button_active span:after {
      transform: rotate(-45deg);
      bottom: 0px; }

.table-container {
  overflow-x: auto; }

@media all and (max-width: 1200px) {
